"use client";

import { motion } from "framer-motion";
import Image from "next/image";
import Link from "next/link";

type BlogPost = {
  id: string;
  title: string;
  slug: string;
  date: string;
  excerpt: string;
  image?: string;
};

interface BlogListProps {
  blogs: BlogPost[];
}

export default function BlogList({ blogs }: BlogListProps) {
  return (
    <section className="max-w-7xl mx-auto grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
      {blogs.map((blog, idx) => (
        <motion.article
          key={blog.id}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: idx * 0.2 }}
          className="bg-white rounded-2xl shadow hover:shadow-lg overflow-hidden flex flex-col"
        >
          {blog.image && (
            <div className="relative w-full h-48">
              <Image
                src={blog.image}
                alt={blog.title}
                fill
                className="object-cover"
              />
            </div>
          )}
          <div className="p-6 flex flex-col flex-1">
            <time className="text-xs text-gray-400">{blog.date}</time>
            <h2 className="text-xl font-semibold mt-2">{blog.title}</h2>
            <p className="text-gray-600 mt-2 flex-1">{blog.excerpt}</p>
            <Link
              href={`/blog/${blog.slug}`}
              className="mt-4 inline-block text-emerald-600 font-medium hover:underline"
            >
              Read More â†’
            </Link>
          </div>
        </motion.article>
      ))}
    </section>
  );
}
